	module.exports = {"data":{"post":{"html":"<p>For those following along with the \"<a href=\"http://www.papascott.de/archives/2011/03/23/lingua-franca/\">which language will Christopher learn in the 6th grade</a>\" contest, we have a winner. A full week before the deadline, he declared his decision for Latin, and we turned in the form to the school today.</p>\n<p>Our preference was for French, and we even tried using to some reverse psychology (pointing out the advantages of Latin and hoping he'd argue with us), but that backfired. The main thing is that he made a decision and is happy with it.</p>\n<p>Thanks to everyone who gave suggestions and played along.</p>","fields":{"title":"And the winner is... Latin","date":"28 Mar 2011","url":"https://papascott.de/2011-03-28-and-the-winner-is-latin/"}}},"pathContext":{"slug":"/2011-03-28-and-the-winner-is-latin/","prev":{"fields":{"title":"Lingua franca","date":"23 Mar 2011","slug":"/2011-03-23-lingua-franca/"}},"next":{"fields":{"title":"Green Card","date":"30 Mar 2011","slug":"/2011-03-30-green-card/"}}}}

